-box;\n    box-sizing: border-box;\n}\n\n//flex 布局和 子元素 对其方式\n@mixin fj($type: space-between) {\n    display: flex;\n    justify-content: $type;\n}\n\n@mixin ellipsis1 {\n    overflow: hidden;\n    white-space: nowrap;\n    text-overflow: ellipsis;\n}\n\n@mixin ellipsis2($num) {\n    display: -webkit-box;\n    overflow: hidden;\n    text-overflow: ellipsis;\n    word-wrap: break-word;\n    white-space: normal !important;\n    -webkit-line-clamp: $num;\n    /*! autoprefixer: off */\n    -webkit-box-orient: vertical;\n}","\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n@import \"../../common/style/mixin.scss\";\n\n.codeLoginWrap {\n  position: relative;\n  display: flex;\n  justify-content: space-between;\n  flex-direction: column;\n  background: rgba(255, 255, 255, 0.85);\n  .title {\n    padding-bottom: 10px;\n    font-size: 28px;\n    color: $titleColor;\n    box-sizing: border-box;\n  }\n  .slider {\n    p {\n      font-size: 14px;\n      color: $textWeakColor;\n      line-height: 35px;\n    }\n  }\n  .fooer {\n    position: absolute;\n    bottom: 20px;\n    align-self: center;\n    font-size: 14px;\n    text-align: center;\n    line-height: 14px;\n    color: $textColor;\n    .footer-btn {\n      cursor: pointer;\n    }\n    .midLine {\n      height: 14px;\n      line-height: 14px;\n      padding: 0 8px;\n    }\n  }\n}\n"]}]);




